body {
    background-color: #282c34;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    margin: 0;
    padding: 0;
    text-align: center;
}

body div div {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
    width: 900px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #61dafb;
    border-radius: 5px;
    box-shadow: 0px 0px 20px 5px #61dafb74;
    transition: all 0.3s ease-in-out;
}

div h1 {
    font-size: 3.5rem;
    margin-bottom: 1rem;
    text-shadow: 0px 5px 2px rgba(0, 0, 0, 0.6);
}

div button {
    background-color: #61dafb;
    border: none;
    border-radius: 5px;
    color: #282c34;
    cursor: pointer;
    font-size: 2.2rem;
    font-weight: bold;
    margin-top: 2rem;
    padding: 0.5rem 1rem;
    transition: all 0.3s ease-in-out;
}

div button:active {
    background-color: #282c34;
    color: #61dafb;
    transform: scale(0.9);
}